import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { asyncAppearGreetingsWindow } from "../../actions/index"

class GreetingsButton extends Component{
  asyncGreetingsButton(){
    return <button onClick={() => this.appearGreetings()}><img alt="" src="data:image/png;base64,"></img></button>
  };
  appearGreetings(){
    setTimeout(() => {this.props.asyncAppearGreetingsWindow(this.props.greetings)}, 3000);
  };
  render(){
    return(
      <div className="app-greetings-page__button">
        {this.asyncGreetingsButton()}
      </div>
    )
  }
};

function mapStateToProps (state) {
  return {
    greetings : state.greetings
  };
}

function matchDispatchToProps (dispatch) {
  return bindActionCreators({ asyncAppearGreetingsWindow }, dispatch);
}

export default connect(mapStateToProps, matchDispatchToProps)(GreetingsButton);